.navbar {
    width: 99.5vw;
    overflow-x: hidden;
    max-width: 60rem;
    margin: 0;
    padding: 0.2rem 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: -0.3rem;
    z-index: 1; /* Ensure navbar stays above other content */
    background-color: #b7f075;
}

.logo {
    object-fit: cover;
    border-width: 4px;
    border-radius: 60%;
    border-color: #16423C;
    height: 3rem;
    width: 3rem;
    margin-right: 1rem;
}

.desktopmenulistitem {
    margin: 1rem;
    cursor: pointer;
    font-size: 1.1rem;
}

.desktopmenulistitem:hover {
    color: #0d593d;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid #0d593d;
}

.desktopmenuimg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 0.5rem;
}

.desktopmmenubtn {
    background-color: white;
    border: none;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.5rem 2rem;
    margin-left: -2rem;
    cursor: pointer;
}

.active {
    color: #16423C;
    font-weight: 800;
}



