#works {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-align: center;
  padding: 0 0.5rem;
  width: 90vw;
  max-width: 53rem;
  overflow: hidden; /* Prevent overflow */
}

.workstitle {
  font-size: 3rem;
  font-weight: 500;
  padding-top: 4rem;
  margin-bottom: 1.5rem;
}

.worksdesc {
  font-weight: 200;
  font-size: 1rem;
  max-width: 50rem;
  padding: 0 2rem;
}

/* Adjust the portfolio container */
.portfolio-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -10px; /* Adjust margins to avoid overflow */
  padding: 0; /* Remove padding to maintain alignment */
  overflow-y: hidden;
}

.portfolio-item {
  margin: 10px; /* Reduced margin for better control */
  text-align: center;
  flex-basis: calc(33.33% - 20px); /* 3 items per row on large screens */
  box-sizing: border-box; /* Include padding in width calculation */
}

.portfolio-item img {
  width: 100%; /* Use 100% to avoid exceeding parent width */
  height: 16rem;
  margin-bottom: 10px;
}

.portfolio-item p {
  width: 100%; /* Use 100% for consistency */
  margin: 0 auto;
  font-size: 14px;
}

/* Media Queries */

/* For medium screens */
@media (max-width: 768px) {
  .portfolio-item {
      flex-basis: calc(50% - 20px); /* 2 items per row on medium screens */
  }
}

/* For small screens */
@media (max-width: 480px) {
  .portfolio-item {
      flex-basis: 100%; /* 1 item per row on small screens */
  }
}

